@font-face {
  font-family: "Roboto-Medium";
  src: url("../fonts/Roboto-Medium.eot"),
       url("../fonts/Roboto-Medium.woff2"),
       url("../fonts/Roboto-Medium.woff"),
       url("../fonts/Roboto-Medium.ttf"),
       url("../fonts/Roboto-Medium.svg");
}

@font-face {
  font-family: "Roboto-Regular";
  src: url("../fonts/Roboto-Regular.eot"),
       url("../fonts/Roboto-Regular.woff2"),
       url("../fonts/Roboto-Regular.woff"),
       url("../fonts/Roboto-Regular.ttf"),
       url("../fonts/Roboto-Regular.svg");
}

.conteiner {
  max-width: 1085px;
  box-sizing: content-box;
  margin-left: auto;
  margin-right: auto;
}
.nuv {
  font-family: "Roboto-Medium";
  display: flex;
  justify-content: space-between;
  &__list {
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 20px;
    padding: 0;
    &__inner {
      list-style: none;
      padding: 0 50px 0 0;
      &:last-child {
        padding: 0;
      }
      &__link {
        text-decoration: none;
        color: #000000;
        &:hover {
          color: rgb(48, 214, 48);
        }
      }
    }
  }
  &::before {
    content: '';
    background: url('../image/FlyTime.svg') no-repeat;
    width: 10%;
    height: 100px;
  }
}
.contact__us {
  font-family: "Roboto-Medium";
  color: rgb(255, 255, 255);
  font-size: 20px;
  border: none;
  background-color: rgb(48, 214, 48);
  padding: 0 5%;
}
.header {
  cursor: default;
  background: url('../image/fonImg.jpg') no-repeat;
  background-size: cover;
  font-family: "Roboto-Regular";
  color: rgb(255, 255, 255);
  padding: 5% 0 10% 0;
  text-align: center;
  position: relative;
  margin-top: 1%;
  &::before {
    content: "";
    display: block;
    background-color: rgba(0, 0, 0, 0.7);
    background-size: 100%;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  &__name {
    font-size: 45px;
    position: relative;
    z-index: 1;
    &__sub {
      position: relative;
      z-index: 1;
    }
  }
}

.option {
  font-family: "Roboto-Regular";
  cursor: default;
  margin-top: 5rem;
  &__titel {
    font-family: "Roboto-Medium";
    font-size: 25px;
  }
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    &__information {
      width: 23%;
      padding: 0 10% 0 0;
      margin-bottom: 5%;
      &:last-child {
        padding: 0;
      }
      &:nth-child(3) {
        padding: 0;
      }
      &__titel {
        font-family: "Roboto-Medium";
        font-size: 45px;
        margin: 0;
      }
      &__discription {
        font-size: 18px;
        color: rgb(105, 105, 105);
      }
      &__link {
        text-decoration: none;
        color: rgba(0, 0, 0, 1);
      }
      &:hover {
        .option__inner__information__link {
          color: rgb(255, 0, 0);
        }
        .option__inner__information__discription {
          color: rgba(0, 0, 0, 1);
        }
      }
    }
  }
}
.block__call {
  font-family: "Roboto-Regular";
  cursor: default;
  margin-top: 5rem;
  &__titel {
    font-family: "Roboto-Medium";
    font-size: 25px;
  }
  &__information {
    display: flex;
    justify-content: space-between;
    &__inner {
      width: 50%;
      &__titel {
        font-family: "Roboto-Medium";
          font-size: 45px;
          margin: 3% 0;
      }
    }
    &__link {
      display: block;
      text-decoration: none;
      color: rgba(255, 255, 255, 1);
      background: rgb(48, 214, 48);
      padding: 0.8rem 1rem;
    }
  }
}
.footer {
  margin-top: 5rem;
}